import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getWorkout, fetchWorkout } from "../../redux/workoutSlice";
import { resetFilter, updateFilter } from "../../redux/filterSlice";

//Components
import WorkoutFilterConfig from "../filterworkouts/filterWorkouts";
import {
  Grid,
  Paper,
  makeStyles,
  IconButton,
  Badge,
  Chip,
  Button,
} from "@material-ui/core";

//Icons
import { Autorenew, FilterList, Clear } from "@material-ui/icons";

const useStyles = makeStyles(() => ({
  root: {
    padding: "10px",
    textAlign: "center",
  },
  content: {
    color: "#30475e",
    display: "flex",
    justifyContent: "center",
    fontSize: "1.1rem",
    fontWeight: "bold",
    minHeight: "180px",
  },
  filterBtn: {
    textAlign: "left",
  },
  loadBtn: {
    textAlign: "right",
  },
  closeBtn: {
    marginTop: "20px",
    fontWeight: "bold",
  },
  filterChips: {
    marginTop: "30px",
    display: "flex",
    flexWrap: "wrap",
  },
  chip: {
    margin: "5px",
    minWidth: "60px",
    maxWidth: "fit-content",
    display: "flex",
    justifyContent: "space-between",
  },
  workout: {
    minWidth: "200px",
    width: "fit-content",
    margin: "auto",
    textAlign: "left",
  },
}));

export default function RandomWorkout() {
  const classes = useStyles();
  const dispatch = useDispatch();

  const workout = useSelector(getWorkout);
  const filter = useSelector((state) => state.filter);
  const isFilterSet = filter.length > 0;
  const [showFilter, toggleFilter] = useState("amrap");

  const handleShowFilter = (tab) => () => {
    if (showFilter) {
      toggleFilter("");
    } else {
      toggleFilter(tab);
    }
  };

  const handleDelete = (todelete) => () => {
    dispatch(updateFilter(todelete));
  };

  const handleCLick = () => {
    dispatch(fetchWorkout());
    toggleFilter("");
  };

  return (
    <Paper elevation={4} className={classes.root}>
      <Grid container>
        <Grid item xs={6} className={classes.filterBtn}>
          <IconButton
            color={filter.length > 0 ? "secondary" : "primary"}
            onClick={handleShowFilter("amrap")}
          >
            <Badge badgeContent={filter.length} color="secondary">
              <FilterList />
            </Badge>
          </IconButton>
        </Grid>
        <Grid item xs={6} className={classes.loadBtn}>
          {!showFilter && isFilterSet && (
            <IconButton onClick={handleCLick} color="secondary">
              <Autorenew />
            </IconButton>
          )}
        </Grid>
        <Grid item xs={12} className={classes.content}>
          {showFilter ? (
            <div>
              <WorkoutFilterConfig tab={showFilter} />
              {isFilterSet && (
                <Button onClick={handleCLick} className={classes.closeBtn}>
                  Load Workout
                </Button>
              )}
            </div>
          ) : (
            <div className={classes.workout}>
              {workout.status === "succeeded" &&
                workout.workout.map((el, i) => <div key={i}>{el}</div>)}
              {workout.status === "failed" && workout.error}
            </div>
          )}
        </Grid>
        <Grid item xs={12} className={classes.filterChips}>
          {isFilterSet && (
            <IconButton
              onClick={() => dispatch(resetFilter())}
              style={{ padding: "5px" }}
            >
              <Clear />
            </IconButton>
          )}
          {filter.map((el, i) => (
            <Chip
              key={i}
              variant="outlined"
              color="secondary"
              size="small"
              label={el}
              onDelete={handleDelete(el)}
              onClick={() => toggleFilter(el)}
              className={classes.chip}
            />
          ))}
        </Grid>
      </Grid>
    </Paper>
  );
}
