import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import { configRequest } from '../requests'

export const fetchConfigs = createAsyncThunk('configs/fetchConfigs', async () => {
     return await configRequest.get();
   })

export const configsSlice = createSlice({
     name: 'configs',
     initialState: {
          status: 'idle',
          error: null,
          configs: {}
     },
     reducers: {
          
     },
     extraReducers: {
          [fetchConfigs.pending]: (state, action) => {
            state.status = 'loading';
          },
          [fetchConfigs.fulfilled]: (state, action) => {
            state.status = 'succeeded';
            state.configs = action.payload.data;
          },
          [fetchConfigs.rejected]: (state, action) => {
            state.status = 'failed';
            state.error = 'Something went terrible wrong! sorry...';
          }
        }
})

// export const {  } = configsSlice.actions

export const getWorkoutConfigs = state => ({
     Style: state.configs.configs.style,
     Duration: state.configs.configs.duration,
     Equipment: state.configs.configs.equipment,
});

export const getConfigStatus = state => state.configs.status;
export const getConfigError = state => state.configs.error;

export default configsSlice.reducer