import axios from 'axios';

const protocol = window.location.protocol;
const hostname = window.location.hostname;
const isHostLocal = ['localhost', '192.'].some(el => hostname.includes(el));
const port = isHostLocal ? ':5000' : '';
const apiUrl = `${protocol}//${hostname}${port}`;

const request = (route) => ({
     get: async (data = '') => (await axios.get(`${apiUrl}${route}`, { params: data })).data
});

//Workout requests
export const workoutRequest = request('/api/workout');

//Workout requests
export const configRequest = request('/api/configs');
