import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import { workoutRequest } from '../requests'

export const fetchWorkout = createAsyncThunk('workout/fetchWorkout', async (_, { getState }) => {
     const filter = getState().filter;
     return await workoutRequest.get({ filter });
})

export const workoutSlice = createSlice({
     name: 'workout',
     initialState: {
          status: 'idle',
          error: null,
          workout: [],
     },
     reducers: {

     },
     extraReducers: {
          [fetchWorkout.pending]: (state, action) => {
               state.status = 'loading';
          },
          [fetchWorkout.fulfilled]: (state, action) => {
               state.status = 'succeeded';
               state.workout = action.payload.data.split('\n');
          },
          [fetchWorkout.rejected]: (state, action) => {
               state.status = 'failed';
               state.workout = [];
               state.error = 'could not load workout';
          }
     }
})

// export const {  } = workoutSlice.actions

export const getWorkout = state => state.workout;

export default workoutSlice.reducer