import './App.css';
import { CircularProgress, makeStyles } from "@material-ui/core";
import RandomWorkout from './components/randomWorkout/randomWorkout';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { fetchConfigs } from './redux/configSlice';

const useStyles = makeStyles(() => ({
  root: {
    display: 'flex',
    width: '100%',
  },
  center: {
    margin: '10% auto',
    width: "90%",
    maxWidth: '600px',
  },
  copyright: {
    position: 'fixed',
    bottom: '5px',
    width: '100%',
    textAlign: 'center',
  }
}))

function App() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const configs = useSelector(state => state.configs);

  useEffect(() => {
    dispatch(fetchConfigs());
  }, [])

  return (
    <div className={classes.root} >
      <div className={classes.center}>
        {configs.status === 'succeeded' && (<RandomWorkout />)}
        {configs.status === 'failed' && (<h5>{configs.error}</h5>)}
      </div>
    </div>
  )
}

export default App;
